import HttpService from './HttpService'

export default {
    sendEmail
}

function sendEmail(name, date, email='', isConfirmation, phone='', duration='', time='', treatments='') {
    const businessName=' '
    let bodyText = ''
    if (isConfirmation) {
        bodyText =`         ${name} ,

          ${businessName} !
           ${treatments}  
         ${date}
         ${time}
            - ${duration} 
             - ${phone}
        
        `
    } else {
        bodyText = `
        ,
         ${name}
           ${date}  
           `
    }
    let emailObj = {
        email,
        bodyText
    }
    return HttpService.post('email',emailObj)
}